  }

  .bg-shape {
    height: 100%;
    border-radius: 0 0 0 349px;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    transform: none;
  }

  section#home {
    height: auto;
    margin-top: 55px;
    padding-top: 31px;
    color: #fff;
  }

  .drawer--right .drawer-hamburger {
    right: 120px;
  }

  .headp-hero-text-area h1,
  .headp-full-feature-content h1,
  .headp-download-content h1 {
