    margin: 8px 0 25px 0;
  }

  .headp-hero-text-area p {
    width: 100%;
  }

  .headp-hero-text-area a.df-btn {
    padding: 12px 17px;
    background: #fff;
    color: #2374f8;
  }

  .headp-hero-image-area-start .single-image {
    height: 282px;
    background-position: center !important;
  }

  .headp-product-content {
    margin: 0;
  }

  .headp-hero-image-area-start {
    margin: 0;
  }

  .headp-product-slider-area .row .col-md-6:nth-child(4),
  .headp-newslater-area-start .row .col-md-6:last-child,
  .headp-product-count-area .row .col-md-6:last-child {
    margin-top: 25px;
  }

  ul.pagination li a {
    padding: 4px 12px;
    margin-right: 8px;
  }

  .headp-contact-area-start form .row .col-md-6 {
    margin-bottom: 15px;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .headp-header-area-start {
    display: block;
  }

  .mobile-menu-area {
    display: none;
  }

  section {
    overflow: hidden;
  }

  body {
    overflow-x: hidden;
  }

  .bg-shape {
    height: 116%;
    border-radius: 280px 0 0 0;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    transform: none;
  }

  section#home {
    height: auto;
    margin-top: 55px;
    padding-top: 31px;
    color: #fff;
  }

  header.headp-header-area-start {
    left: 0;
    top: 0;
  }

  .headp-hero-image-area-start {
    height: auto;
  }

  .headp-hero-image-area-start .single-image {
    height: 350px;
  }

  .headp-hero-text-area h1,
  .headp-full-feature-content h1,
  .headp-download-content h1 {
    font-size: 49px;
    line-height: 49px;
  }

  .headp-hero-text-area a.df-btn {
    background: #fff;
    color: #2374f8;
  }

  ul.headp-main-menu li a {
    padding: 10px;
  }

  .headp-download-content a > img {
    width: 170px;
  }

  .single-product-count .media-head i {
    font-size: 20px;
    width: 40px;
    height: 40px;
  }

  .single-product-count .media-body p,
  .single-product-count .media-body p > span {
    font-size: 25px;
  }

  .single-product-count .media-body h4 {
    font-size: 17px;
  }

  .headp-single-contact-form .media-body span {
    font-size: 13px;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .mobile-menu-area {
    display: none;
  }

  .headp-header-area-start {
    display: block;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .mobile-menu-area {
    display: none;
  }

  .headp-header-area-start {
    display: block;
  }
}
